/**
 * State manager for tracking extraction progress per session
 *
 * Stores last read line number for each session to enable
 * incremental extraction without re-reading entire files
 */

import { mkdir, readFile, writeFile } from "node:fs/promises";
import { join } from "node:path";
import { CLAUDE_ZEST_DIR } from "../config/constants.js";
import { logger } from "./logger.js";

const STATE_DIR = join(CLAUDE_ZEST_DIR, "state");

interface SessionState {
  sessionId: string;
  lastReadLine: number;
  lastMessageIndex: number; // Last message_index that was queued
  filePath: string;
}

/**
 * Get state file path for a session
 */
function getStateFilePath(sessionId: string): string {
  return join(STATE_DIR, `${sessionId}.json`);
}

/**
 * Ensure state directory exists
 */
async function ensureStateDir(): Promise<void> {
  try {
    await mkdir(STATE_DIR, { recursive: true });
  } catch (error) {
    logger.debug("State directory already exists or error creating:", error);
  }
}

/**
 * Read session state
 */
export async function readSessionState(sessionId: string): Promise<SessionState | null> {
  try {
    const stateFile = getStateFilePath(sessionId);
    const content = await readFile(stateFile, "utf-8");
    return JSON.parse(content) as SessionState;
  } catch (error) {
    // State file doesn't exist yet - this is normal for new sessions
    logger.debug(`No state found for session ${sessionId} (new session)`);
    return null;
  }
}

/**
 * Write session state
 */
export async function writeSessionState(state: SessionState): Promise<void> {
  try {
    await ensureStateDir();
    const stateFile = getStateFilePath(state.sessionId);
    await writeFile(stateFile, JSON.stringify(state, null, 2), "utf-8");
    logger.debug(
      `Updated state for session ${state.sessionId}: lastReadLine=${state.lastReadLine}`,
    );
  } catch (error) {
    logger.error(`Failed to write state for session ${state.sessionId}:`, error);
  }
}

/**
 * Update last read line number and last message index for a session
 */
export async function updateLastReadLine(
  sessionId: string,
  filePath: string,
  lineNumber: number,
  lastMessageIndex: number,
): Promise<void> {
  const newState: SessionState = {
    sessionId,
    lastReadLine: lineNumber,
    lastMessageIndex,
    filePath,
  };
  await writeSessionState(newState);
}
